import { ShoppingCart } from "lucide-react"
import { Button } from "../ui/button"
import { Sheet, SheetContent, SheetTrigger } from "../ui/sheet"

const SheetHeader = () => {
  return (
    <Sheet>
      <SheetTrigger asChild>
          <Button size="icon" className="bg-slate-800 text-gray-200 mr-9 hover:bg-slate-800 cursor-pointer">
              <ShoppingCart size={100}/>
              <span className="sr-only">Abrir / Fechar</span>
          </Button>
      </SheetTrigger>
      <SheetContent side="right" className="sm:w-xs bg-slate-800 text-white border-0 shadow-md">
          
      </SheetContent>
    </Sheet>
  )
}

export default SheetHeader